import Link from 'next/link'
import { useRouter } from 'next/router'
import { motion } from 'framer-motion'

const mainNavigation = [
  { name: 'Home', href: '/dashboard', icon: HomeIcon },
  { name: 'Inbox', href: '/inbox', icon: InboxIcon, badge: true },
  { name: 'Chat', href: '/chat', icon: ChatIcon, badge: true },
  { name: 'Projects', href: '/projects', icon: ProjectsIcon },
  { name: 'Tasks', href: '/tasks', icon: TasksIcon },
  { name: 'Docs', href: '/docs', icon: DocsIcon },
]

const secondaryNavigation = [
  { name: 'Calendar', href: '/calendar', icon: CalendarIcon },
  { name: 'Automations', href: '/automations', icon: AutomationsIcon },
  { name: 'Reports', href: '/reports', icon: ReportsIcon },
]

const bottomNavigation = [
  { name: 'Settings', href: '/settings', icon: SettingsIcon },
]

export default function LeftSidebar() {
  const router = useRouter()
  const currentPath = router.pathname

  return (
    <aside className="w-64 h-screen bg-[#0a0a0f] border-r border-white/10 flex flex-col fixed left-0 top-0 z-40">
      {/* Logo / Workspace Switcher */}
      <div className="p-4 border-b border-white/10">
        <Link href="/dashboard" className="flex items-center gap-3">
          <div className="w-8 h-8 bg-gradient-to-br from-amber-300 to-amber-500 rounded-lg flex items-center justify-center">
            <span className="text-[#0a0a0f] font-bold text-sm">K</span>
          </div>
          <div>
            <span className="text-lg font-light text-white">Kingdom</span>
            <span className="text-lg font-semibold text-amber-400">X</span>
          </div>
        </Link>
      </div>

      {/* Main Navigation */}
      <nav className="flex-1 overflow-y-auto py-4 px-3 space-y-1">
        {mainNavigation.map((item) => (
          <NavItem
            key={item.name}
            item={item}
            isActive={currentPath === item.href}
          />
        ))}

        {/* Divider */}
        <div className="my-4 border-t border-white/10" />

        {secondaryNavigation.map((item) => (
          <NavItem
            key={item.name}
            item={item}
            isActive={currentPath === item.href}
          />
        ))}
      </nav>

      {/* Bottom Navigation */}
      <div className="p-3 border-t border-white/10 space-y-1">
        {bottomNavigation.map((item) => (
          <NavItem
            key={item.name}
            item={item}
            isActive={currentPath === item.href}
          />
        ))}
      </div>
    </aside>
  )
}

function NavItem({ item, isActive }: { item: any; isActive: boolean }) {
  const Icon = item.icon
  const isDisabled = item.comingSoon

  return (
    <Link
      href={isDisabled ? '#' : item.href}
      className={`
        flex items-center gap-3 px-3 py-2.5 rounded-lg text-sm font-medium transition-all duration-200
        ${isActive 
          ? 'bg-amber-500/10 text-amber-400 border border-amber-500/20' 
          : 'text-white/60 hover:text-white hover:bg-white/5 border border-transparent'
        }
        ${isDisabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
      `}
    >
      <Icon className={`w-5 h-5 ${isActive ? 'text-amber-400' : 'text-white/50'}`} />
      <span className="flex-1">{item.name}</span>
      {item.badge && (
        <span className="w-2 h-2 bg-amber-500 rounded-full" />
      )}
      {item.comingSoon && (
        <span className="text-xs text-white/30 px-1.5 py-0.5 bg-white/5 rounded">Soon</span>
      )}
    </Link>
  )
}

